/**
 * Signal Controller
 * ä¿¡å·åˆ¶å¾¡ã‚·ã‚¹ãƒ†ãƒ 
 *
 * å›ºå®šã‚µã‚¤ã‚¯ãƒ«æ–¹å¼ã§4æ–¹å‘ã®ä¿¡å·æ©Ÿã‚’å”èª¿åˆ¶å¾¡
 */

import { TrafficLight } from './TrafficLight';
import type { Direction, SignalPhase, SignalControlConfig } from './types';

/**
 * ä¿¡å·åˆ¶å¾¡ãƒ•ã‚§ãƒ¼ã‚º
 */
type ControlPhase =
  | 'northSouthGreen'
  | 'northSouthYellow'
  | 'allRed1'
  | 'eastWestGreen'
  | 'eastWestYellow'
  | 'allRed2';

export class SignalController {
  private readonly trafficLights: Map<Direction, TrafficLight>;
  private readonly config: SignalControlConfig;

  // ç¾åœ¨ã®ãƒ•ã‚§ãƒ¼ã‚ºã¨çµŒéæ™‚é–“
  private currentPhase: ControlPhase;
  private timeInPhase: number;
  private cycleCount: number;

  constructor(
    trafficLights: Map<Direction, TrafficLight>,
    config: SignalControlConfig
  ) {
    this.trafficLights = trafficLights;
    this.config = config;

    // åˆæœŸçŠ¶æ…‹
    this.currentPhase = 'northSouthGreen';
    this.timeInPhase = 0;
    this.cycleCount = 0;

    // åˆæœŸãƒ•ã‚§ãƒ¼ã‚ºã‚’å„ä¿¡å·æ©Ÿã«è¨­å®š
    this.applyCurrentPhase();
  }

  /**
   * ä¿¡å·åˆ¶å¾¡ã®æ›´æ–°ï¼ˆæ¯ã‚¿ã‚¤ãƒ ã‚¹ãƒ†ãƒƒãƒ—å‘¼ã³å‡ºã—ï¼‰
   * @param dt - ã‚¿ã‚¤ãƒ ã‚¹ãƒ†ãƒƒãƒ—ï¼ˆç§’ï¼‰
   */
  update(dt: number): void {
    this.timeInPhase += dt;

    // å„ä¿¡å·æ©Ÿã®å†…éƒ¨æ™‚è¨ˆã‚’æ›´æ–°
    for (const light of this.trafficLights.values()) {
      light.tick(dt);
    }

    // ãƒ•ã‚§ãƒ¼ã‚ºåˆ‡ã‚Šæ›¿ãˆã®ãƒã‚§ãƒƒã‚¯
    if (this.shouldAdvancePhase()) {
      this.advancePhase();
    }
  }

  /**
   * æ¬¡ã®ãƒ•ã‚§ãƒ¼ã‚ºã«é€²ã‚€ã¹ãã‹ãƒã‚§ãƒƒã‚¯
   * @returns ãƒ•ã‚§ãƒ¼ã‚ºåˆ‡ã‚Šæ›¿ãˆãŒå¿…è¦ãªå ´åˆtrue
   */
  private shouldAdvancePhase(): boolean {
    const phaseDuration = this.getPhaseDuration(this.currentPhase);
    return this.timeInPhase >= phaseDuration;
  }

  /**
   * ç¾åœ¨ã®ãƒ•ã‚§ãƒ¼ã‚ºã®ç¶™ç¶šæ™‚é–“ã‚’å–å¾—
   * @param phase - åˆ¶å¾¡ãƒ•ã‚§ãƒ¼ã‚º
   * @returns ç¶™ç¶šæ™‚é–“ï¼ˆç§’ï¼‰
   */
  private getPhaseDuration(phase: ControlPhase): number {
    switch (phase) {
      case 'northSouthGreen':
        return this.config.greenDuration.northSouth;
      case 'northSouthYellow':
        return this.config.yellowDuration;
      case 'allRed1':
        return this.config.allRedDuration;
      case 'eastWestGreen':
        return this.config.greenDuration.eastWest;
      case 'eastWestYellow':
        return this.config.yellowDuration;
      case 'allRed2':
        return this.config.allRedDuration;
    }
  }

  /**
   * æ¬¡ã®ãƒ•ã‚§ãƒ¼ã‚ºã«é€²ã‚€
   */
  private advancePhase(): void {
    // ãƒ•ã‚§ãƒ¼ã‚ºé·ç§»
    switch (this.currentPhase) {
      case 'northSouthGreen':
        this.currentPhase = 'northSouthYellow';
        break;
      case 'northSouthYellow':
        this.currentPhase = 'allRed1';
        break;
      case 'allRed1':
        this.currentPhase = 'eastWestGreen';
        break;
      case 'eastWestGreen':
        this.currentPhase = 'eastWestYellow';
        break;
      case 'eastWestYellow':
        this.currentPhase = 'allRed2';
        break;
      case 'allRed2':
        this.currentPhase = 'northSouthGreen';
        this.cycleCount++;
        break;
    }

    // æ–°ã—ã„ãƒ•ã‚§ãƒ¼ã‚ºã‚’å„ä¿¡å·æ©Ÿã«é©ç”¨
    this.timeInPhase = 0;
    this.applyCurrentPhase();
  }

  /**
   * ç¾åœ¨ã®ãƒ•ã‚§ãƒ¼ã‚ºã‚’å„ä¿¡å·æ©Ÿã«é©ç”¨
   */
  private applyCurrentPhase(): void {
    const phases = this.getSignalPhases(this.currentPhase);

    for (const [direction, phase] of Object.entries(phases)) {
      const light = this.trafficLights.get(direction as Direction);
      if (light) {
        light.setPhase(phase);
      }
    }
  }

  /**
   * åˆ¶å¾¡ãƒ•ã‚§ãƒ¼ã‚ºã‹ã‚‰å„æ–¹å‘ã®ä¿¡å·ãƒ•ã‚§ãƒ¼ã‚ºã‚’å–å¾—
   * @param controlPhase - åˆ¶å¾¡ãƒ•ã‚§ãƒ¼ã‚º
   * @returns å„æ–¹å‘ã®ä¿¡å·ãƒ•ã‚§ãƒ¼ã‚º
   */
  private getSignalPhases(
    controlPhase: ControlPhase
  ): Record<Direction, SignalPhase> {
    switch (controlPhase) {
      case 'northSouthGreen':
        return {
          north: 'green',
          south: 'green',
          east: 'red',
          west: 'red',
        };
      case 'northSouthYellow':
        return {
          north: 'yellow',
          south: 'yellow',
          east: 'red',
          west: 'red',
        };
      case 'allRed1':
      case 'allRed2':
        return {
          north: 'red',
          south: 'red',
          east: 'red',
          west: 'red',
        };
      case 'eastWestGreen':
        return {
          north: 'red',
          south: 'red',
          east: 'green',
          west: 'green',
        };
      case 'eastWestYellow':
        return {
          north: 'red',
          south: 'red',
          east: 'yellow',
          west: 'yellow',
        };
    }
  }

  /**
   * æŒ‡å®šæ–¹å‘ã®ä¿¡å·æ©Ÿã‚’å–å¾—
   * @param direction - æ–¹å‘
   * @returns ä¿¡å·æ©Ÿã€å­˜åœ¨ã—ãªã„å ´åˆnull
   */
  getTrafficLight(direction: Direction): TrafficLight | null {
    return this.trafficLights.get(direction) ?? null;
  }

  /**
   * ç¾åœ¨ã®ã‚µã‚¤ã‚¯ãƒ«ç•ªå·ã‚’å–å¾—
   * @returns ã‚µã‚¤ã‚¯ãƒ«ç•ªå·
   */
  getCycleCount(): number {
    return this.cycleCount;
  }

  /**
   * ç¾åœ¨ã®ã‚µã‚¤ã‚¯ãƒ«å†…çµŒéæ™‚é–“ã‚’å–å¾—
   * @returns çµŒéæ™‚é–“ï¼ˆç§’ï¼‰
   */
  getTimeInCycle(): number {
    // ç¾åœ¨ã®ãƒ•ã‚§ãƒ¼ã‚ºã¾ã§ã®ç´¯ç©æ™‚é–“
    let elapsedTime = 0;

    const phaseOrder: ControlPhase[] = [
      'northSouthGreen',
      'northSouthYellow',
      'allRed1',
      'eastWestGreen',
      'eastWestYellow',
      'allRed2',
    ];

    for (const phase of phaseOrder) {
      if (phase === this.currentPhase) {
        break;
      }
      elapsedTime += this.getPhaseDuration(phase);
    }

    return elapsedTime + this.timeInPhase;
  }

  /**
   * ã‚µã‚¤ã‚¯ãƒ«é•·ã‚’å–å¾—
   * @returns ã‚µã‚¤ã‚¯ãƒ«é•·ï¼ˆç§’ï¼‰
   */
  getCycleLength(): number {
    return this.config.cycleLength;
  }

  /**
   * ãƒªã‚»ãƒƒãƒˆ
   */
  reset(): void {
    this.currentPhase = 'northSouthGreen';
    this.timeInPhase = 0;
    this.cycleCount = 0;

    // ã™ã¹ã¦ã®ä¿¡å·æ©Ÿã‚’ãƒªã‚»ãƒƒãƒˆ
    for (const light of this.trafficLights.values()) {
      light.reset('red');
    }

    // åˆæœŸãƒ•ã‚§ãƒ¼ã‚ºã‚’é©ç”¨
    this.applyCurrentPhase();
  }

  /**
   * çµ±è¨ˆæƒ…å ±ã®å–å¾—
   */
  getStats() {
    return {
      currentPhase: this.currentPhase,
      timeInPhase: this.timeInPhase,
      cycleCount: this.cycleCount,
      timeInCycle: this.getTimeInCycle(),
      cycleLength: this.config.cycleLength,
    };
  }

  /**
   * ãƒ‡ãƒãƒƒã‚°ç”¨æ–‡å­—åˆ—è¡¨ç¾
   */
  toString(): string {
    return `SignalController(phase=${this.currentPhase}, cycle=${this.cycleCount}, time=${this.timeInPhase.toFixed(1)}s)`;
  }
}
