/**
 * Vehicle Renderer
 * è»Šä¸¡æç”»ã‚·ã‚¹ãƒ†ãƒ 
 *
 * è»Šä¸¡ã®è¦–è¦šçš„è¡¨ç¾ã‚’æç”»
 */

import { Vehicle } from './Vehicle';
import type { Direction } from './types';

export class VehicleRenderer {
  private readonly ctx: CanvasRenderingContext2D;

  constructor(ctx: CanvasRenderingContext2D) {
    this.ctx = ctx;
  }

  /**
   * è»Šä¸¡ã‚’æç”»
   * @param vehicle - è»Šä¸¡ã‚ªãƒ–ã‚¸ã‚§ã‚¯ãƒˆ
   */
  render(vehicle: Vehicle): void {
    this.ctx.save();

    // è»Šä¸¡ã®ä½ç½®ã«ç§»å‹•
    this.ctx.translate(vehicle.position.x, vehicle.position.y);

    // è»Šä¸¡ã®å‘ãã«å›è»¢
    const angle = this.getRotationAngle(vehicle.direction);
    this.ctx.rotate(angle);

    // Yè»¸ã®åè»¢ã‚’è£œæ­£ï¼ˆè»Šä¸¡ã¯æ­£ã—ã„å‘ãã«è¡¨ç¤ºï¼‰
    this.ctx.scale(1, -1);

    // è»Šä¸¡ã®çŸ©å½¢ã‚’æç”»
    const width = 2.0; // è»Šå¹…ï¼ˆmï¼‰
    const length = vehicle.length; // è»Šé•·ï¼ˆmï¼‰

    // è»Šä¸¡ã®è‰²ã‚’æ±ºå®šï¼ˆã‚¹ãƒ†ãƒ¼ã‚¿ã‚¹ã¨é€²è¡Œæ„å›³ã«åŸºã¥ãï¼‰
    const color = this.getVehicleColor(vehicle);

    this.ctx.fillStyle = color;
    this.ctx.fillRect(-width / 2, -length / 2, width, length);

    // è»Šä¸¡ã®å¢ƒç•Œç·š
    this.ctx.strokeStyle = '#000000';
    this.ctx.lineWidth = 0.15;
    this.ctx.strokeRect(-width / 2, -length / 2, width, length);

    // é€²è¡Œæ–¹å‘ã‚¤ãƒ³ã‚¸ã‚±ãƒ¼ã‚¿ãƒ¼ï¼ˆè»Šä¸¡å‰éƒ¨ï¼‰
    this.ctx.fillStyle = '#ffffff';
    this.ctx.fillRect(-width / 3, length / 2 - 0.5, width * 2 / 3, 0.3);

    this.ctx.restore();
  }

  /**
   * æ–¹å‘ã‹ã‚‰å›è»¢è§’åº¦ã‚’å–å¾—
   */
  private getRotationAngle(direction: Direction): number {
    switch (direction) {
      case 'north':
        return Math.PI / 2; // 90åº¦
      case 'south':
        return -Math.PI / 2; // -90åº¦
      case 'east':
        return 0; // 0åº¦
      case 'west':
        return Math.PI; // 180åº¦
    }
  }

  /**
   * è»Šä¸¡ã®è‰²ã‚’å–å¾—
   */
  private getVehicleColor(vehicle: Vehicle): string {
    // ã‚¹ãƒ†ãƒ¼ã‚¿ã‚¹ã«ã‚ˆã‚‹è‰²åˆ†ã‘
    if (vehicle.status === 'waiting') {
      return '#e74c3c'; // èµ¤ï¼ˆå¾…æ©Ÿä¸­ï¼‰
    } else if (vehicle.velocity < 1) {
      return '#f39c12'; // ã‚ªãƒ¬ãƒ³ã‚¸ï¼ˆä½é€Ÿï¼‰
    }

    // é€²è¡Œæ„å›³ã«ã‚ˆã‚‹è‰²åˆ†ã‘
    switch (vehicle.turnIntent) {
      case 'straight':
        return '#3498db'; // é’ï¼ˆç›´é€²ï¼‰
      case 'left':
        return '#9b59b6'; // ç´«ï¼ˆå·¦æŠ˜ï¼‰
      case 'right':
        return '#2ecc71'; // ç·‘ï¼ˆå³æŠ˜ï¼‰
      default:
        return '#95a5a6'; // ã‚°ãƒ¬ãƒ¼
    }
  }
}
