/**
 * 2D Vector Utility Class
 * 2æ¬¡å…ƒãƒ™ã‚¯ãƒˆãƒ«æ¼”ç®—ãƒ¦ãƒ¼ãƒ†ã‚£ãƒªãƒ†ã‚£
 */

export class Vector2D {
  constructor(
    public x: number,
    public y: number
  ) {}

  /**
   * ãƒ™ã‚¯ãƒˆãƒ«ã®åŠ ç®—
   */
  add(other: Vector2D): Vector2D {
    return new Vector2D(this.x + other.x, this.y + other.y);
  }

  /**
   * ãƒ™ã‚¯ãƒˆãƒ«ã®æ¸›ç®—
   */
  subtract(other: Vector2D): Vector2D {
    return new Vector2D(this.x - other.x, this.y - other.y);
  }

  /**
   * ã‚¹ã‚«ãƒ©ãƒ¼å€
   */
  scale(scalar: number): Vector2D {
    return new Vector2D(this.x * scalar, this.y * scalar);
  }

  /**
   * ãƒ™ã‚¯ãƒˆãƒ«ã®å¤§ãã•ï¼ˆé•·ã•ï¼‰
   */
  magnitude(): number {
    return Math.sqrt(this.x * this.x + this.y * this.y);
  }

  /**
   * æ­£è¦åŒ–ï¼ˆå˜ä½ãƒ™ã‚¯ãƒˆãƒ«åŒ–ï¼‰
   */
  normalize(): Vector2D {
    const mag = this.magnitude();
    if (mag === 0) {
      return new Vector2D(0, 0);
    }
    return new Vector2D(this.x / mag, this.y / mag);
  }

  /**
   * ä»–ã®ãƒ™ã‚¯ãƒˆãƒ«ã¨ã®è·é›¢
   */
  distance(other: Vector2D): number {
    const dx = this.x - other.x;
    const dy = this.y - other.y;
    return Math.sqrt(dx * dx + dy * dy);
  }

  /**
   * å†…ç©
   */
  dot(other: Vector2D): number {
    return this.x * other.x + this.y * other.y;
  }

  /**
   * ãƒ™ã‚¯ãƒˆãƒ«ã®ã‚³ãƒ”ãƒ¼
   */
  clone(): Vector2D {
    return new Vector2D(this.x, this.y);
  }

  /**
   * æ–‡å­—åˆ—è¡¨ç¾
   */
  toString(): string {
    return `Vector2D(${this.x.toFixed(2)}, ${this.y.toFixed(2)})`;
  }

  /**
   * ç­‰ä¾¡åˆ¤å®š
   */
  equals(other: Vector2D, epsilon: number = 0.001): boolean {
    return (
      Math.abs(this.x - other.x) < epsilon &&
      Math.abs(this.y - other.y) < epsilon
    );
  }

  /**
   * ã‚¼ãƒ­ãƒ™ã‚¯ãƒˆãƒ«ã‹ã©ã†ã‹
   */
  isZero(epsilon: number = 0.001): boolean {
    return Math.abs(this.x) < epsilon && Math.abs(this.y) < epsilon;
  }

  /**
   * é™çš„ãƒ¡ã‚½ãƒƒãƒ‰: ã‚¼ãƒ­ãƒ™ã‚¯ãƒˆãƒ«
   */
  static zero(): Vector2D {
    return new Vector2D(0, 0);
  }

  /**
   * é™çš„ãƒ¡ã‚½ãƒƒãƒ‰: å˜ä½ãƒ™ã‚¯ãƒˆãƒ«ï¼ˆXæ–¹å‘ï¼‰
   */
  static unitX(): Vector2D {
    return new Vector2D(1, 0);
  }

  /**
   * é™çš„ãƒ¡ã‚½ãƒƒãƒ‰: å˜ä½ãƒ™ã‚¯ãƒˆãƒ«ï¼ˆYæ–¹å‘ï¼‰
   */
  static unitY(): Vector2D {
    return new Vector2D(0, 1);
  }

  /**
   * é™çš„ãƒ¡ã‚½ãƒƒãƒ‰: 2ç‚¹é–“ã®è·é›¢
   */
  static distance(a: Vector2D, b: Vector2D): number {
    return a.distance(b);
  }

  /**
   * é™çš„ãƒ¡ã‚½ãƒƒãƒ‰: ç·šå½¢è£œé–“
   */
  static lerp(a: Vector2D, b: Vector2D, t: number): Vector2D {
    return new Vector2D(
      a.x + (b.x - a.x) * t,
      a.y + (b.y - a.y) * t
    );
  }
}
