/**
 * Traffic Light Renderer
 * ä¿¡å·æ©Ÿæç”»ã‚·ã‚¹ãƒ†ãƒ 
 *
 * ä¿¡å·æ©Ÿã®è¦–è¦šçš„è¡¨ç¾ã‚’æç”»
 */

import { TrafficLight } from './TrafficLight';

export class TrafficLightRenderer {
  private readonly ctx: CanvasRenderingContext2D;

  constructor(ctx: CanvasRenderingContext2D) {
    this.ctx = ctx;
  }

  /**
   * ä¿¡å·æ©Ÿã‚’æç”»
   * @param light - ä¿¡å·æ©Ÿã‚ªãƒ–ã‚¸ã‚§ã‚¯ãƒˆ
   */
  render(light: TrafficLight): void {
    this.ctx.save();

    // ä¿¡å·æ©Ÿã®ä½ç½®ã«ç§»å‹•
    this.ctx.translate(light.position.x, light.position.y);

    // Yè»¸ã®åè»¢ã‚’è£œæ­£
    this.ctx.scale(1, -1);

    // ä¿¡å·æ©Ÿã®ãƒœãƒƒã‚¯ã‚¹ã‚’æç”»
    const boxWidth = 1.5;
    const boxHeight = 4.0;

    this.ctx.fillStyle = '#2c3e50'; // ãƒ€ãƒ¼ã‚¯ã‚°ãƒ¬ãƒ¼
    this.ctx.fillRect(-boxWidth / 2, -boxHeight / 2, boxWidth, boxHeight);

    // ä¿¡å·æ©Ÿã®ãƒ©ã‚¤ãƒˆã‚’æç”»ï¼ˆä¸Šã‹ã‚‰èµ¤ã€é»„ã€ç·‘ï¼‰
    const lightRadius = 0.5;
    const lightSpacing = 1.2;

    // èµ¤ä¿¡å·ãƒ©ã‚¤ãƒˆ
    this.drawLight(0, boxHeight / 2 - lightSpacing, lightRadius, light.phase === 'red');

    // é»„ä¿¡å·ãƒ©ã‚¤ãƒˆ
    this.drawLight(0, boxHeight / 2 - lightSpacing * 2, lightRadius, light.phase === 'yellow');

    // ç·‘ä¿¡å·ãƒ©ã‚¤ãƒˆ
    this.drawLight(0, boxHeight / 2 - lightSpacing * 3, lightRadius, light.phase === 'green');

    // ä¿¡å·æ©Ÿã®æ ç·š
    this.ctx.strokeStyle = '#1a252f';
    this.ctx.lineWidth = 0.1;
    this.ctx.strokeRect(-boxWidth / 2, -boxHeight / 2, boxWidth, boxHeight);

    this.ctx.restore();
  }

  /**
   * ä¿¡å·ãƒ©ã‚¤ãƒˆã‚’æç”»
   * @param x - Xåº§æ¨™ï¼ˆä¿¡å·æ©Ÿãƒ­ãƒ¼ã‚«ãƒ«åº§æ¨™ï¼‰
   * @param y - Yåº§æ¨™ï¼ˆä¿¡å·æ©Ÿãƒ­ãƒ¼ã‚«ãƒ«åº§æ¨™ï¼‰
   * @param radius - ãƒ©ã‚¤ãƒˆã®åŠå¾„
   * @param active - ãƒ©ã‚¤ãƒˆãŒç‚¹ç¯ã—ã¦ã„ã‚‹ã‹
   */
  private drawLight(x: number, y: number, radius: number, active: boolean): void {
    this.ctx.save();

    // ãƒ©ã‚¤ãƒˆã®èƒŒæ™¯ï¼ˆå¸¸ã«æš—ã„å††ï¼‰
    this.ctx.fillStyle = active ? this.getActiveLightColor() : '#34495e';
    this.ctx.beginPath();
    this.ctx.arc(x, y, radius, 0, Math.PI * 2);
    this.ctx.fill();

    // ãƒ©ã‚¤ãƒˆã®å¢ƒç•Œç·š
    this.ctx.strokeStyle = '#1a252f';
    this.ctx.lineWidth = 0.05;
    this.ctx.stroke();

    // ç‚¹ç¯æ™‚ã®ã‚°ãƒ­ãƒ¼åŠ¹æœ
    if (active) {
      this.ctx.shadowColor = this.getActiveLightColor();
      this.ctx.shadowBlur = 10;
      this.ctx.beginPath();
      this.ctx.arc(x, y, radius * 0.7, 0, Math.PI * 2);
      this.ctx.fillStyle = this.getBrightLightColor();
      this.ctx.fill();
    }

    this.ctx.restore();
  }

  /**
   * ã‚¢ã‚¯ãƒ†ã‚£ãƒ–ãªãƒ©ã‚¤ãƒˆã®è‰²ã‚’å–å¾—
   */
  private getActiveLightColor(): string {
    // ç¾åœ¨ã®ãƒ•ã‚§ãƒ¼ã‚ºã«åŸºã¥ã„ã¦è‰²ã‚’è¿”ã™
    // ã“ã®é–¢æ•°ã¯drawLightã‹ã‚‰å‘¼ã°ã‚Œã‚‹ãŸã‚ã€phaseæƒ…å ±ãŒå¿…è¦
    // ç°¡ç•¥åŒ–ã®ãŸã‚ã€ã“ã“ã§ã¯ãƒ™ãƒ¼ã‚¹è‰²ã‚’è¿”ã™
    return '#e74c3c'; // ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆã¯èµ¤
  }

  /**
   * æ˜ã‚‹ã„ãƒ©ã‚¤ãƒˆã®è‰²ã‚’å–å¾—
   */
  private getBrightLightColor(): string {
    return '#ffffff'; // ç™½
  }
}
