/**
 * Traffic Light Simulation - Main Entry Point
 * äº¤å·®ç‚¹ä¿¡å·æ©Ÿã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³ - ãƒ¡ã‚¤ãƒ³ã‚¨ãƒ³ãƒˆãƒªãƒ¼ãƒã‚¤ãƒ³ãƒˆ
 */

import { Simulation } from './Simulation';
import { Renderer } from './Renderer';
import { ControlPanel } from './ControlPanel';
import { ParameterEditor } from './ParameterEditor';
import { ResultsPanel } from './ResultsPanel';
import { InfoDisplay } from './InfoDisplay';
import { ChartManager } from './ChartManager';
import { DEFAULT_CONFIG } from './config';

console.log('äº¤å·®ç‚¹ä¿¡å·æ©Ÿã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³èµ·å‹•ä¸­...');

// ã‚°ãƒ­ãƒ¼ãƒãƒ«çŠ¶æ…‹
let simulation: Simulation;
let renderer: Renderer;
let controlPanel: ControlPanel;
let resultsPanel: ResultsPanel;
let infoDisplay: InfoDisplay;
let chartManager: ChartManager;

/**
 * ã‚¢ãƒ—ãƒªã‚±ãƒ¼ã‚·ãƒ§ãƒ³ã®åˆæœŸåŒ–
 */
function initializeApp(): void {
  try {
    // Canvasè¦ç´ ã®å–å¾—
    const canvas = document.getElementById('simulation-canvas') as HTMLCanvasElement;
    if (!canvas) {
      throw new Error('Canvas element not found');
    }

    // ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³ã®åˆæœŸåŒ–
    simulation = new Simulation(DEFAULT_CONFIG);
    console.log('âœ… Simulation initialized');

    // ãƒ¬ãƒ³ãƒ€ãƒ©ãƒ¼ã®åˆæœŸåŒ–
    renderer = new Renderer(canvas);
    console.log('âœ… Renderer initialized');

    // UIã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã®åˆæœŸåŒ–
    controlPanel = new ControlPanel(simulation);
    controlPanel.setOnComplete(handleSimulationComplete);
    console.log('âœ… Control panel initialized');

    // Initialize parameter editor (accessible via DOM)
    new ParameterEditor('parameter-editor', DEFAULT_CONFIG);
    console.log('âœ… Parameter editor initialized');

    resultsPanel = new ResultsPanel('results-panel');
    console.log('âœ… Results panel initialized');

    infoDisplay = new InfoDisplay('info-display');
    console.log('âœ… Info display initialized');

    chartManager = new ChartManager();
    console.log('âœ… Chart manager initialized');

    // ã‚¢ãƒ‹ãƒ¡ãƒ¼ã‚·ãƒ§ãƒ³ãƒ«ãƒ¼ãƒ—ã®é–‹å§‹
    startAnimationLoop();
    console.log('âœ… Animation loop started');

    // ã‚¦ã‚£ãƒ³ãƒ‰ã‚¦ãƒªã‚µã‚¤ã‚ºã‚¤ãƒ™ãƒ³ãƒˆ
    window.addEventListener('resize', () => {
      renderer.resizeCanvas();
    });

    console.log('âœ… ã‚¢ãƒ—ãƒªã‚±ãƒ¼ã‚·ãƒ§ãƒ³åˆæœŸåŒ–å®Œäº†');
  } catch (error) {
    console.error('âŒ ã‚¢ãƒ—ãƒªã‚±ãƒ¼ã‚·ãƒ§ãƒ³åˆæœŸåŒ–ã‚¨ãƒ©ãƒ¼:', error);
    alert(`åˆæœŸåŒ–ã‚¨ãƒ©ãƒ¼: ${error}`);
  }
}

/**
 * ã‚¢ãƒ‹ãƒ¡ãƒ¼ã‚·ãƒ§ãƒ³ãƒ«ãƒ¼ãƒ—
 */
function startAnimationLoop(): void {
  function animate() {
    // ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³ã®çŠ¶æ…‹ã«å¿œã˜ã¦å‡¦ç†
    if (simulation.getState() === 'running') {
      simulation.step();
      controlPanel.updateButtonStates();
      controlPanel.updateProgress(simulation.getProgress());
    }

    // æç”»
    renderer.render(simulation);

    // æƒ…å ±è¡¨ç¤ºã®æ›´æ–°
    infoDisplay.update(simulation);

    // æ¬¡ã®ãƒ•ãƒ¬ãƒ¼ãƒ ã‚’ãƒªã‚¯ã‚¨ã‚¹ãƒˆ
    requestAnimationFrame(animate);
  }

  animate();
}

/**
 * ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³å®Œäº†æ™‚ã®ãƒãƒ³ãƒ‰ãƒ©
 */
function handleSimulationComplete(): void {
  console.log('âœ… ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³å®Œäº†');

  // çµæœã‚’å–å¾—
  const results = simulation.getResults();

  // çµ±è¨ˆã‚’è¡¨ç¤º
  infoDisplay.displayStatistics(results.statistics);

  // çµæœãƒ‘ãƒãƒ«ã‚’æ›´æ–°
  resultsPanel.displayResults(results);

  // ã‚°ãƒ©ãƒ•ã‚’ä½œæˆ
  chartManager.createAllCharts(results);

  // ãƒœã‚¿ãƒ³ã®çŠ¶æ…‹ã‚’æ›´æ–°
  controlPanel.updateButtonStates();

  alert('ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³ãŒå®Œäº†ã—ã¾ã—ãŸï¼çµæœã‚’ç¢ºèªã—ã¦ãã ã•ã„ã€‚');
}

/**
 * ã‚¨ãƒ©ãƒ¼ãƒãƒ³ãƒ‰ãƒªãƒ³ã‚°
 */
window.addEventListener('error', (event) => {
  console.error('ã‚°ãƒ­ãƒ¼ãƒãƒ«ã‚¨ãƒ©ãƒ¼:', event.error);
});

window.addEventListener('unhandledrejection', (event) => {
  console.error('æœªå‡¦ç†ã®Promiseæ‹’å¦:', event.reason);
});

// DOMContentLoadedã‚¤ãƒ™ãƒ³ãƒˆã§ã‚¢ãƒ—ãƒªã‚±ãƒ¼ã‚·ãƒ§ãƒ³ã‚’åˆæœŸåŒ–
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', initializeApp);
} else {
  initializeApp();
}
