/**
 * Main Renderer
 * ãƒ¡ã‚¤ãƒ³ãƒ¬ãƒ³ãƒ€ãƒ©ãƒ¼
 *
 * Canvasæç”»ã®çµ±æ‹¬ç®¡ç†
 */

import { Simulation } from './Simulation';
import { IntersectionRenderer } from './IntersectionRenderer';
import { VehicleRenderer } from './VehicleRenderer';
import { TrafficLightRenderer } from './TrafficLightRenderer';

export class Renderer {
  private readonly canvas: HTMLCanvasElement;
  private readonly ctx: CanvasRenderingContext2D;

  // ã‚µãƒ–ãƒ¬ãƒ³ãƒ€ãƒ©ãƒ¼
  private readonly intersectionRenderer: IntersectionRenderer;
  private readonly vehicleRenderer: VehicleRenderer;
  private readonly trafficLightRenderer: TrafficLightRenderer;

  // ã‚«ãƒ¡ãƒ©ãƒ‘ãƒ©ãƒ¡ãƒ¼ã‚¿
  private scale: number = 1.5; // ãƒ”ã‚¯ã‚»ãƒ«/ãƒ¡ãƒ¼ãƒˆãƒ«
  private offsetX: number = 0;
  private offsetY: number = 0;

  constructor(canvas: HTMLCanvasElement) {
    this.canvas = canvas;

    const ctx = canvas.getContext('2d');
    if (!ctx) {
      throw new Error('Failed to get 2D rendering context');
    }
    this.ctx = ctx;

    // ã‚µãƒ–ãƒ¬ãƒ³ãƒ€ãƒ©ãƒ¼ã®åˆæœŸåŒ–
    this.intersectionRenderer = new IntersectionRenderer(ctx);
    this.vehicleRenderer = new VehicleRenderer(ctx);
    this.trafficLightRenderer = new TrafficLightRenderer(ctx);

    // Canvasã‚µã‚¤ã‚ºã®è¨­å®š
    this.resizeCanvas();
  }

  /**
   * Canvasã‚µã‚¤ã‚ºã‚’èª¿æ•´
   */
  resizeCanvas(): void {
    const parent = this.canvas.parentElement;
    if (parent) {
      this.canvas.width = parent.clientWidth;
      this.canvas.height = parent.clientHeight;
    }
  }

  /**
   * ãƒ¡ã‚¤ãƒ³æç”»é–¢æ•°
   * @param simulation - ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³ã‚¤ãƒ³ã‚¹ã‚¿ãƒ³ã‚¹
   */
  render(simulation: Simulation): void {
    // ã‚­ãƒ£ãƒ³ãƒã‚¹ã‚’ã‚¯ãƒªã‚¢
    this.clear();

    // åº§æ¨™å¤‰æ›ã‚’é©ç”¨
    this.ctx.save();
    this.applyTransform();

    // 1. äº¤å·®ç‚¹ã‚’æç”»
    this.intersectionRenderer.render(simulation.getIntersection());

    // 2. è»Šä¸¡ã‚’æç”»
    const vehicles = simulation.getAllVehicles();
    for (const vehicle of vehicles) {
      this.vehicleRenderer.render(vehicle);
    }

    // 3. ä¿¡å·æ©Ÿã‚’æç”»
    const trafficLights = simulation.getAllTrafficLights();
    for (const light of trafficLights) {
      this.trafficLightRenderer.render(light);
    }

    // åº§æ¨™å¤‰æ›ã‚’å…ƒã«æˆ»ã™
    this.ctx.restore();
  }

  /**
   * ã‚­ãƒ£ãƒ³ãƒã‚¹ã‚’ã‚¯ãƒªã‚¢
   */
  clear(): void {
    this.ctx.fillStyle = '#f5f5f5'; // èƒŒæ™¯è‰²
    this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
  }

  /**
   * åº§æ¨™å¤‰æ›ã‚’é©ç”¨
   */
  private applyTransform(): void {
    // ã‚­ãƒ£ãƒ³ãƒã‚¹ä¸­å¿ƒã«åŸç‚¹ã‚’ç§»å‹•
    this.ctx.translate(this.canvas.width / 2, this.canvas.height / 2);

    // ã‚¹ã‚±ãƒ¼ãƒ«ã‚’é©ç”¨
    this.ctx.scale(this.scale, this.scale);

    // ã‚ªãƒ•ã‚»ãƒƒãƒˆã‚’é©ç”¨
    this.ctx.translate(this.offsetX, this.offsetY);

    // Yè»¸ã‚’åè»¢ï¼ˆæ•°å­¦çš„ãªåº§æ¨™ç³»ã«ã™ã‚‹ï¼‰
    this.ctx.scale(1, -1);
  }

  /**
   * ã‚¹ã‚±ãƒ¼ãƒ«ã‚’è¨­å®š
   * @param scale - ãƒ”ã‚¯ã‚»ãƒ«/ãƒ¡ãƒ¼ãƒˆãƒ«
   */
  setScale(scale: number): void {
    this.scale = Math.max(0.5, Math.min(5, scale));
  }

  /**
   * ã‚¹ã‚±ãƒ¼ãƒ«ã‚’å–å¾—
   */
  getScale(): number {
    return this.scale;
  }

  /**
   * ã‚ªãƒ•ã‚»ãƒƒãƒˆã‚’è¨­å®š
   * @param x - Xæ–¹å‘ã®ã‚ªãƒ•ã‚»ãƒƒãƒˆï¼ˆãƒ¡ãƒ¼ãƒˆãƒ«ï¼‰
   * @param y - Yæ–¹å‘ã®ã‚ªãƒ•ã‚»ãƒƒãƒˆï¼ˆãƒ¡ãƒ¼ãƒˆãƒ«ï¼‰
   */
  setOffset(x: number, y: number): void {
    this.offsetX = x;
    this.offsetY = y;
  }

  /**
   * ã‚ºãƒ¼ãƒ ã‚¤ãƒ³
   */
  zoomIn(): void {
    this.setScale(this.scale * 1.2);
  }

  /**
   * ã‚ºãƒ¼ãƒ ã‚¢ã‚¦ãƒˆ
   */
  zoomOut(): void {
    this.setScale(this.scale / 1.2);
  }

  /**
   * ãƒ“ãƒ¥ãƒ¼ã‚’ãƒªã‚»ãƒƒãƒˆ
   */
  resetView(): void {
    this.scale = 1.5;
    this.offsetX = 0;
    this.offsetY = 0;
  }

  /**
   * ã‚¹ã‚¯ãƒªãƒ¼ãƒ³åº§æ¨™ã‚’ãƒ¯ãƒ¼ãƒ«ãƒ‰åº§æ¨™ã«å¤‰æ›
   * @param screenX - ã‚¹ã‚¯ãƒªãƒ¼ãƒ³Xåº§æ¨™
   * @param screenY - ã‚¹ã‚¯ãƒªãƒ¼ãƒ³Yåº§æ¨™
   * @returns ãƒ¯ãƒ¼ãƒ«ãƒ‰åº§æ¨™
   */
  screenToWorld(screenX: number, screenY: number): { x: number; y: number } {
    const centerX = this.canvas.width / 2;
    const centerY = this.canvas.height / 2;

    const worldX = (screenX - centerX) / this.scale - this.offsetX;
    const worldY = -((screenY - centerY) / this.scale - this.offsetY);

    return { x: worldX, y: worldY };
  }

  /**
   * ãƒ¯ãƒ¼ãƒ«ãƒ‰åº§æ¨™ã‚’ã‚¹ã‚¯ãƒªãƒ¼ãƒ³åº§æ¨™ã«å¤‰æ›
   * @param worldX - ãƒ¯ãƒ¼ãƒ«ãƒ‰Xåº§æ¨™
   * @param worldY - ãƒ¯ãƒ¼ãƒ«ãƒ‰Yåº§æ¨™
   * @returns ã‚¹ã‚¯ãƒªãƒ¼ãƒ³åº§æ¨™
   */
  worldToScreen(worldX: number, worldY: number): { x: number; y: number } {
    const centerX = this.canvas.width / 2;
    const centerY = this.canvas.height / 2;

    const screenX = (worldX + this.offsetX) * this.scale + centerX;
    const screenY = -(worldY + this.offsetY) * this.scale + centerY;

    return { x: screenX, y: screenY };
  }

  /**
   * Canvasã‚’å–å¾—
   */
  getCanvas(): HTMLCanvasElement {
    return this.canvas;
  }

  /**
   * ã‚³ãƒ³ãƒ†ã‚­ã‚¹ãƒˆã‚’å–å¾—
   */
  getContext(): CanvasRenderingContext2D {
    return this.ctx;
  }
}
