import { Vector2D } from './Vector2D';
import type { Direction, SignalPhase, TrafficLightConfig } from './types';

export class TrafficLight {
  // è­˜åˆ¥æƒ…å ±
  public readonly id: string;
  public readonly direction: Direction;
  public readonly position: Vector2D;

  // ä¿¡å·çŠ¶æ…‹
  public phase: SignalPhase;
  public timeInPhase: number;

  // ä¿¡å·ã‚µã‚¤ã‚¯ãƒ«ãƒ‘ãƒ©ãƒ¡ãƒ¼ã‚¿
  public readonly greenDuration: number;
  public readonly yellowDuration: number;
  public readonly allRedDuration: number;

  constructor(config: TrafficLightConfig) {
    // è­˜åˆ¥æƒ…å ±
    this.id = config.id;
    this.direction = config.direction;
    this.position = new Vector2D(config.position.x, config.position.y);

    // ä¿¡å·çŠ¶æ…‹
    this.phase = config.initialPhase ?? 'red';
    this.timeInPhase = 0;

    // ä¿¡å·ã‚µã‚¤ã‚¯ãƒ«ãƒ‘ãƒ©ãƒ¡ãƒ¼ã‚¿
    this.greenDuration = config.greenDuration;
    this.yellowDuration = config.yellowDuration;
    this.allRedDuration = config.allRedDuration;
  }

  /**
   * ä¿¡å·ãƒ•ã‚§ãƒ¼ã‚ºã‚’è¨­å®šï¼ˆå¤–éƒ¨åˆ¶å¾¡ã«ã‚ˆã‚‹ï¼‰
   * @param newPhase - æ–°ã—ã„ä¿¡å·ãƒ•ã‚§ãƒ¼ã‚º
   */
  setPhase(newPhase: SignalPhase): void {
    if (this.phase !== newPhase) {
      this.phase = newPhase;
      this.timeInPhase = 0;
    }
  }

  /**
   * ãƒ•ã‚§ãƒ¼ã‚ºå†…ã®çµŒéæ™‚é–“ã‚’é€²ã‚ã‚‹
   * @param dt - ã‚¿ã‚¤ãƒ ã‚¹ãƒ†ãƒƒãƒ—ï¼ˆç§’ï¼‰
   */
  tick(dt: number): void {
    this.timeInPhase += dt;
  }

  /**
   * è»Šä¸¡ãŒé€šéå¯èƒ½ã‹ã©ã†ã‹
   * @returns é’ä¿¡å·ã®å ´åˆtrue
   */
  canPass(): boolean {
    return this.phase === 'green';
  }

  /**
   * ç¾åœ¨ã®ãƒ•ã‚§ãƒ¼ã‚ºãŒçµ‚äº†ã—ãŸã‹ã©ã†ã‹
   * @returns ãƒ•ã‚§ãƒ¼ã‚ºçµ‚äº†ã®å ´åˆtrue
   */
  isPhaseComplete(): boolean {
    switch (this.phase) {
      case 'green':
        return this.timeInPhase >= this.greenDuration;
      case 'yellow':
        return this.timeInPhase >= this.yellowDuration;
      case 'red':
        // èµ¤ä¿¡å·ã®çµ‚äº†ã¯å¤–éƒ¨åˆ¶å¾¡ã«ã‚ˆã‚‹
        return false;
      default:
        return false;
    }
  }

  /**
   * æ¬¡ã®ãƒ•ã‚§ãƒ¼ã‚ºã«ç§»è¡Œï¼ˆå˜ç‹¬ã®ä¿¡å·æ©Ÿã®å ´åˆï¼‰
   * æ³¨: å®Ÿéš›ã®ä½¿ç”¨ã§ã¯ SignalController ãŒåˆ¶å¾¡ã—ã¾ã™
   */
  advancePhase(): void {
    switch (this.phase) {
      case 'green':
        this.setPhase('yellow');
        break;
      case 'yellow':
        this.setPhase('red');
        break;
      case 'red':
        // èµ¤ä¿¡å·ã‹ã‚‰é’ä¿¡å·ã¸ã®ç§»è¡Œã¯å¤–éƒ¨åˆ¶å¾¡
        break;
    }
  }

  /**
   * ä¿¡å·ã®è‰²ã‚’å–å¾—ï¼ˆæç”»ç”¨ï¼‰
   * @returns RGBè‰²æ–‡å­—åˆ—
   */
  getColor(): string {
    switch (this.phase) {
      case 'green':
        return '#2ecc71'; // ç·‘
      case 'yellow':
        return '#f39c12'; // é»„è‰²
      case 'red':
        return '#e74c3c'; // èµ¤
      default:
        return '#95a5a6'; // ã‚°ãƒ¬ãƒ¼ï¼ˆã‚¨ãƒ©ãƒ¼æ™‚ï¼‰
    }
  }

  /**
   * æ®‹ã‚Šæ™‚é–“ã‚’å–å¾—
   * @returns ç¾åœ¨ã®ãƒ•ã‚§ãƒ¼ã‚ºã®æ®‹ã‚Šæ™‚é–“ï¼ˆç§’ï¼‰
   */
  getRemainingTime(): number {
    switch (this.phase) {
      case 'green':
        return Math.max(0, this.greenDuration - this.timeInPhase);
      case 'yellow':
        return Math.max(0, this.yellowDuration - this.timeInPhase);
      case 'red':
        return 0; // èµ¤ä¿¡å·ã®æ®‹ã‚Šæ™‚é–“ã¯å¤–éƒ¨åˆ¶å¾¡ã«ã‚ˆã‚‹
      default:
        return 0;
    }
  }

  /**
   * ä¿¡å·çŠ¶æ…‹ã®ãƒªã‚»ãƒƒãƒˆ
   */
  reset(initialPhase: SignalPhase = 'red'): void {
    this.phase = initialPhase;
    this.timeInPhase = 0;
  }

  /**
   * ãƒ‡ãƒãƒƒã‚°ç”¨æ–‡å­—åˆ—è¡¨ç¾
   */
  toString(): string {
    return `TrafficLight(${this.id}, ${this.direction}, ${this.phase}, ${this.timeInPhase.toFixed(1)}s)`;
  }

  /**
   * ä¿¡å·çŠ¶æ…‹ã®ã‚¹ãƒŠãƒƒãƒ—ã‚·ãƒ§ãƒƒãƒˆå–å¾—
   */
  getSnapshot() {
    return {
      id: this.id,
      direction: this.direction,
      phase: this.phase,
      timeInPhase: this.timeInPhase,
      remainingTime: this.getRemainingTime(),
    };
  }
}
